/* container : 공통 */
.container {
  width: 1300px;
  max-width: 100%;
  margin: 0 auto;
}

/* wrap-title : 공통 */
.wrap__title {
  padding: 10rem 0 1rem;
}

.wrap__title h1 {
  color: #000;
  font-size: 10vmax;
  line-height: 0.85;
  letter-spacing: -0.0275em;
  font-family: "Rajdhani";
  text-transform: uppercase;
}

.wrap__title h1 strong {
  display: block;
  opacity: 1;
}

.wrap__title.light h1 {
  color: #fff;
}

.wrap__title h1 strong,
.wrap__title h1 em {
  display: block;
  overflow: hidden;
  padding-top: 10px;
}

.wrap__title h1 span {
  opacity: 1;
  display: inline-block;
  transform: translateY(110%);
}

.light .wrap__title h1 {
  color: #000;
}

/* 모든 페이지의 헤더
#header
.header__inner
.header__bg
.header__nav
.header__footer
*/
#header {
  overflow: hidden;
}

.header__inner {
  z-index: 1200;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  color: #fff;
  border-bottom: 1px solid #2e2e30;
  display: flex;
  align-items: center;
  font-family: "Rajdhani", "Chosunilbo_myungjo";
  font-weight: 500;
  background: #000;
}

.header__inner__port {
  flex: 1 1 35%;
  padding-left: 1rem;
}

.header__inner__port a {
  position: relative;
  padding-left: 10px;
}

.header__inner__port a::before {
  content: "";
  width: 5px;
  height: 5px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 3px;
  border-radius: 50%;
}

.header__inner__port a::after {
  content: "";
  width: 5px;
  height: 5px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 11px;
  border-radius: 50%;
}

.header__inner__logo {
  flex: 1 1 30%;
  text-align: center;
  margin: 1.5rem;
}

.header__inner__logo a {
  font-family: "Rajdhani";
  font-size: 1.8rem;
  line-height: 1;
}

.header__inner__menu {
  flex: 1 1 35%;
  text-align: right;
  padding-right: 1rem;
}

.header__inner__menu a {
  display: inline-block;
  width: 40px;
  height: 40px;
  position: relative;
  margin-top: 5px;
}

.header__inner__menu a::before {
  content: "";
  position: absolute;
  left: 10px;
  top: 10px;
  width: 8px;
  height: 8px;
  background: #fff;
  box-shadow: 0 12px 0 #fff;
}

.header__inner__menu a::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 10px;
  width: 8px;
  height: 8px;
  background: #fff;
  box-shadow: 0 12px 0 #fff;
}

.header__bg {
  z-index: 1100;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.header__bg > div {
  position: absolute;
  left: 0;
  top: 0;
  width: 10vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.83);
}

.header__bg > div:nth-child(1) {
  left: 0vw;
}

.header__bg > div:nth-child(2) {
  left: 10vw;
}

.header__bg > div:nth-child(3) {
  left: 20vw;
}

.header__bg > div:nth-child(4) {
  left: 30vw;
}

.header__bg > div:nth-child(5) {
  left: 40vw;
}

.header__bg > div:nth-child(6) {
  left: 50vw;
}

.header__bg > div:nth-child(7) {
  left: 60vw;
}

.header__bg > div:nth-child(8) {
  left: 70vw;
}

.header__bg > div:nth-child(9) {
  left: 80vw;
}

.header__bg > div:nth-child(10) {
  left: 90vw;
}

.header__nav {
  z-index: 1200;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
}

.header__nav li a {
  font-family: "Rajdhani", "Chosunilbo_myungjo";
  font-size: 5vw;
  line-height: 1.2;
  font-weight: 100;
}

.header__nav ul:last-child li {
  display: inline-block;
  margin-top: 20px;
}

.header__nav ul:last-child li a {
  font-family: "Chosunilbo_myungjo";
  font-size: 16px;
  line-height: 1;
  padding: 10px;
}

.header__nav.demo {
  position: fixed;
  right: 60px;
  top: 26px;
  z-index: 1300;
  left: auto;
  transform: none;
  visibility: visible;
  text-align: right;
  display: flex;
  flex-direction: column-reverse;
}

.header__nav.demo li {
  display: inline;
  margin-top: 0;
}

.header__nav.demo li a {
  transform: translateY(0);
  font-family: "Rajdhani", "Chosunilbo_myungjo";
  font-size: 16px;
  font-weight: 400;
  padding: 2px 5px;
}

.header__nav.demo ul:last-child li {
  margin-top: 0;
}

.header__nav.demo ul:last-child li a {
  padding: 2px 5px;
}

.header__info {
  z-index: 1200;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: #fff;
  border-top: 1px solid #2e2e30;
  display: flex;
  align-items: center;
  font-family: "Rajdhani", "Chosunilbo_myungjo";
  font-weight: 500;
  background: #000;
}

#none.header__info {
  display: none;
}

.header__info > div {
  text-align: center;
  padding: 30px;
}

.header__info > div h4,
.header__info > div p,
.header__info > div ul {
  display: inline-block;
}

.header__info > div h4 {
  font-family: "Rajdhani", "Chosunilbo_myungjo";
  font-weight: 500;
  opacity: 0.6;
  padding: 0 10px;
}

.header__info > div:nth-child(1) {
  flex: 1 1 25%;
  border-right: 1px solid #2e2e30;
}

.header__info > div:nth-child(2) {
  flex: 1 1 20%;
  border-right: 1px solid #2e2e30;
}

.header__info > div:nth-child(3) {
  flex: 1 1 55%;
}

.header__info > div:nth-child(3) li {
  display: inline-block;
  padding: 0 10px;
}

/* header light */
.light .header__inner {
  background: #f0eeeb;
  border-bottom: 1px solid #dad8d1;
}

.light .header__inner__port a {
  color: #000;
}

.light .header__inner__logo a {
  color: #000;
}

.light .header__nav.demo li a {
  color: #000;
}

.light .header__inner__menu a::before {
  background: #000;
  box-shadow: 0 12px 0 #000;
}

.light .header__inner__menu a::after {
  background: #000;
  box-shadow: 0 12px 0 #000;
}

.light .header__inner__port a::before {
  background: #000;
}

.light .header__inner__port a::after {
  background: #000;
}

/* header Animation */
.header__nav {
  visibility: hidden;
}

.header__nav li {
  overflow: hidden;
}

.header__nav li a {
  display: inline-block;
  transform: translateY(90%);
}

.header__bg {
  visibility: hidden;
}

.header__bg > div {
  width: 0;
  pointer-events: none;
}

/* 모든 페이지의 푸터
#footer
.footer__inner
*/
#footer {
  background-color: #000;
  height: 100vh;
}

.footer__inner {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
}

.footer__inner p {
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 80px;
  font-family: "Rajdhani", "Chosunilbo_myungjo";
}

.footer__inner h3 {
  font-size: 144px;
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 5rem;
  font-family: "Rajdhani";
}

.footer__inner a {
  color: #fff;
  border: 1px solid #fff;
  border-radius: 50px;
  padding: 18px 53px 18px 53px;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  font-family: "Rajdhani", "Chosunilbo_myungjo";
}

/* clearfix */
.clearfix {
  *zoom: 1;
}
.clearfix:before,
.clearfix:after {
  display: block;
  content: "";
  line-height: 0;
}
.clearfix:after {
  clear: both;
}
.list > ul {
  display: flex;
  flex-wrap: wrap;
}
.list > ul > li {
  width: 300px;
  margin: 10px 10px;
}
