// main
#mainCont {
  height: 100vh;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main__cont {
  color: #fff;
  font-size: 8vw;
  line-height: 1;
  letter-spacing: -0.0275em;
  transition: all 2s;
  font-family: "Rajdhani";
  font-weight: 400;
  text-transform: uppercase;
}

//header
#aboutCont {
  background-color: #f0eeeb;
}

.about-cont .about {
  padding-bottom: 250px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.about-cont .about:nth-child(1) .about-img {
  flex: 0 0 100%;
}

.about-cont .about:nth-child(1) .about-txt {
  flex: 0 0 100%;
}

.about-cont .about:nth-child(1) .about-txt h2 {
  margin-top: 2em;
}

.about-cont .about:nth-child(2) .about-img {
  flex: 0 0 64%;
}

.about-cont .about:nth-child(2) .about-txt {
  flex: 0 0 30%;
}

.about-cont .about:nth-child(3) {
  flex-direction: row-reverse;
}

.about-cont .about:nth-child(3) .about-img {
  flex: 0 0 54%;
}

.about-cont .about:nth-child(3) .about-txt {
  flex: 0 0 40%;
}

.about-cont .about .about-txt {
  font-family: "Chosunilbo_myungjo";
}

.about-cont .about .about-txt h2 {
  margin: 0 0 1em;
  font-size: 1.625rem;
}

.about-cont .about .about-txt p {
  line-height: 1.6;
  font-size: 16px;
}

/* referCont */
#referCont {
  background-color: #f0eeeb;
  height: auto;
}

.refer-cont {
  padding-top: 200px;
  padding-bottom: 200px;
}
.refer-cont2 {
  padding-top: 200px;
  background-color: #f0eeeb;
  height: 100vh;
}

.refer-cont .refer-title li {
  display: inline;
  font-family: "Rajdhani";
}

.refer-cont .refer-title li a {
  font-size: 1.2rem;
  display: inline-block;
  padding: 0.5rem 2rem;
  border: 1px solid #222;
  border-radius: 5px;
  margin-bottom: 1rem;
  margin-right: 10px;
}

.refer-cont .refer-title li.active a {
  background: #000;
  color: #fff;
}

.refer-cont .refer-table {
  border-bottom: 2px solid #000;
  margin-top: 1.2rem;
}

.refer-cont .refer-table li {
  margin: 0;
  list-style: none;
  border-top: 1px solid #000;
  position: relative;
  transition: all 0.3s;
}

.refer-cont .refer-table li:hover {
  background: rgba(0, 0, 0, 0.081);
}

.refer-cont .refer-table li span {
  display: inline-block;
  padding: 25px 10px;
  font-size: 14px;
  font-family: "S-CoreDream-3Light";
  box-sizing: border-box;
}

.refer-cont .refer-table li span.alpha {
  position: absolute;
  left: 0;
  top: 0;
}

.refer-cont .refer-table li span.attr {
  padding-left: 10%;
  width: 35%;
}

.refer-cont .refer-table li span.desc {
  width: 64.5%;
}

/* youtubeCont */
#youtubeCont {
  background-color: #f0eeeb;
}

.youtube-cont {
  padding-bottom: 200px;
}

.site {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 200px;
}

.site h2 {
  font-size: 40px;
  padding-bottom: 20px;
  flex: 0 0 100%;
  font-family: "Chosunilbo_myungjo";
}

.site .left {
  flex: 0 0 35%;
}

.site .right {
  font-family: "Chosunilbo_myungjo";
  font-size: 18px;
  flex: 0 0 60%;
}

.site .right dl {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.site .right dt {
  flex: 0 0 20%;
}

.site .right dd {
  flex: 0 0 80%;
  padding-bottom: 30px;
}

/* scriptCont */
#scriptCont {
  background-color: #f0eeeb;
}

.title {
  text-align: right;
  margin-bottom: 10px;
}
.title h2 {
  font-family: "Chosunilbo_myungjo";
}
.youtube_search_input {
  border: none;
  width: 250px;
  height: 40px;
  border-radius: 5px;
}
.youtube_search_btn {
  border: none;
  background: #000;
  color: #fff;
  width: 50px;
  height: 40px;
  border-radius: 5px;
}
.script-cont {
  padding-bottom: 0px;
}

.script {
  padding-bottom: 200px;
}

.script h2 {
  font-family: "Chosunilbo_myungjo";
  font-size: 40px;
  padding-bottom: 10px;
}

.script .view {
  width: 100%;
}

.script .desc {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.script .desc h3 {
  flex: 0 0 100%;
  font-size: 30px;
  line-height: 2em;
  margin-top: 20px;
  font-family: "Chosunilbo_myungjo";
}

.script .desc > div {
  flex: 0 0 31%;
  font-family: "Chosunilbo_myungjo";
}

.script .desc > div.num span {
  border: 1px solid #9b9b9b;
  width: 50px;
  height: 50px;
  display: inline-block;
  border-radius: 50%;
  text-align: center;
  line-height: 48px;
  margin: 0px 0px 10px 7px;
  color: #030406;
}

.script .desc > div.num span.active {
  border-style: dashed;
}

/* contactCont */
#contactCont {
  height: 100vh;
  background-color: #000;
}

.contact-cont {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

.contact-cont div {
  font-size: 100px;
  font-family: "Rajdhani";
  font-weight: normal;
  padding-top: 1.6vw;
  text-transform: uppercase;
  color: #fff;
  font-size: 180px;
  line-height: 130px;
}

/* 포트폴리오 페이지 
.port__cont 
.port__item 
.port__item__img
.port__item__txt
*/
#portCont {
  background: #000;
  padding-bottom: 200px;
}

.port__cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.port__item {
  flex: 0 0 32%;
  margin-bottom: 2%;
}

.port__item__txt {
  padding: 35px 35px 25px 35px;
  background: #151517;
}

.port__item__txt h2 {
  font-family: "Cinzel", serif;
  font-size: 50px;
  line-height: 1;
  color: #fff;
}

.port__item__txt p {
  font-size: 16px;
  color: #b0aead;
  font-family: "Rajdhani";
}

/*트레일러*/
.overlay {
  background: rgba(0, 0, 0, 0.92);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
}
.modal {
  position: absolute;
}
.modal_close {
  display: block;
  width: 32px;
  height: 32px;
  overflow: hidden;
  position: absolute;
  top: 24px;
  right: 24px;
  text-indent: 100%;
}
.modal_on {
  overflow: hidden;
}
