/* 로딩 소스 */
#loader {
  position: fixed;
  z-index: 99999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transition: width 0s 1.4s ease;
}
.loader__icon {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  opacity: 0;
  transition: opacity 0.5s ease;
}
.loader__icon svg {
  transform-origin: 0 0;
}
.loader__box {
  position: absolute;
  left: 0;
  width: 0;
  height: 20%;
  background-color: #f0eeeb;
  background-color: #000;
  transition: width 0.7s ease;
}
.light .loader__box {
  background-color: #f0eeeb;
}
.circle1 {
  fill: #fff;
}
.circle2 {
  fill: #fff;
}
.light .loader__box {
  background-color: #f0eeeb;
}
.light .circle1 {
  fill: #000;
}
.light .circle2 {
  fill: #000;
}
.loader__box:nth-child(0) {
  top: calc(-1 * 20%);
  transition-delay: -0.2s;
}
.loader__box:nth-child(1) {
  top: calc(0 * 20%);
  transition-delay: 0s;
}
.loader__box:nth-child(2) {
  top: calc(1 * 20%);
  transition-delay: 0.2s;
}
.loader__box:nth-child(3) {
  top: calc(2 * 20%);
  transition-delay: 0.4s;
}
.loader__box:nth-child(4) {
  top: calc(3 * 20%);
  transition-delay: 0.6s;
}
.loader__box:nth-child(5) {
  top: calc(4 * 20%);
  transition-delay: 0.8s;
}
.loader__active {
  width: 100%;
  transition-delay: 0s;
}
.loader__active .loader__icon {
  opacity: 1;
  transition: opacity 0.5s 1.4s ease;
}
.loader__active .loader__box {
  width: 100%;
}
.loader__active .loader__box:nth-child(0) {
  transition-delay: -0.2s;
}
.loader__active .loader__box:nth-child(1) {
  transition-delay: 0s;
}
.loader__active .loader__box:nth-child(2) {
  transition-delay: 0.2s;
}
.loader__active .loader__box:nth-child(3) {
  transition-delay: 0.4s;
}
.loader__active .loader__box:nth-child(4) {
  transition-delay: 0.6s;
}
.loader__active .loader__box:nth-child(5) {
  transition-delay: 0.8s;
}
